<template>
  <div class="visualization-page">
    <h1>数据可视化</h1>
    <div class="charts-container">
      <div class="charts-grid">
        <div v-for="(chart, index) in ['tech', 'modality', 'phase']" 
             :key="index" 
             class="chart-item">
          <div :ref="'chart' + index" style="height: 400px;"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts'

export default {
  name: 'VisualizationPage',
  data() {
    return {
      charts: [],
      pieData: {
        pie1: [["上海", 1154], ["北京", 1064], 
               ["成都", 784], ["武汉", 701], 
               ["杭州", 583], ["深圳", 546], 
               ["长沙", 467], ["广州", 460], ["西安", 447], 
               ["南京", 405], ["重庆", 423], ["苏州", 365]],
        pie2: [["1h", 8.4], ["1-2h", 57.5], 
               ["2-3h", 27.7], [">=3h", 6.4]],
        pie3: [["剧本杀APP", 26.7],["电视节目",25.9],["朋友推荐",22.9],["桌游店",6.3],["其他",2.0],["社交媒体",16.1]]
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.initCharts()
      // 添加窗口大小改变时的自适应
      window.addEventListener('resize', this.handleResize)
    })
  },
  methods: {
    initCharts() {
      const charts = ['pie3', 'pie1', 'pie2']
      const titles = ['首次了解剧本杀的渠道占比', '典型城市线下剧本杀门店数量', '对剧本杀游戏时长的接受度比例']
      
      charts.forEach((key, index) => {
        if (this.$refs['chart' + index] && this.$refs['chart' + index][0]) {
          const chart = echarts.init(this.$refs['chart' + index][0])
          const options = this.getChartOptions(this.pieData[key], titles[index])
          chart.setOption(options)
          this.charts.push(chart)
        }
      })
    },
    handleResize() {
      this.charts.forEach(chart => chart.resize())
    },
    getChartOptions(data, title) {
      return {
        title: {
          text: title,
          left: 'center',
          textStyle: {
            fontSize: 14,
            fontWeight: 500,
            fontFamily: 'Microsoft YaHei'
          }
        },
        legend: {
          orient: 'vertical',
          left: 'left',
          top: '10%',
          icon: 'circle',
          textStyle: {
            fontFamily: 'Microsoft YaHei'
          },
          itemGap: 10,
          itemWidth: 14,
          itemHeight: 14
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}: {c}'
        },
        series: [{
          type: 'pie',
          radius: ['30%', '50%'],
          center: ['50%', '50%'],
          avoidLabelOverlap: true,
          label: {
            show: false,
          },
          emphasis: {
            label: {
              show: false,
              fontSize: '20',
              fontWeight: 'bold'
            }
          },
          labelLine: {
            show: true,
          },
          data: this.transformData(data)
        }]
      }
    },
    transformData(dataList) {
      return dataList.map(([name, value]) => ({
        name: name.length <= 4 ? name : name.slice(0, 4) + '...',
        value,
        tooltip: {
          formatter: `${name}: ${value}`
        }
      }))
    }
  },
  beforeUnmount() {
    this.charts.forEach(chart => chart.dispose())
    window.removeEventListener('resize', this.handleResize)
  }
}
</script>

<style scoped>
.visualization-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.charts-container {
  margin-top: 30px;
  padding: 20px;
  background: var(--card-bg);
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.charts-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  padding: 20px;
}

.chart-item {
  background: white;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .charts-grid {
    grid-template-columns: 1fr;
  }
  
  .chart-item {
    height: 400px;
  }
}
</style> 