<template>
  <div id="app" class="app-container">
    <nav class="nav-menu">
      <div class="menu-icon" @click="isMenuOpen = !isMenuOpen" v-show="isMobile">
        <span></span>
        <span></span>
        <span></span>
      </div>
      
      <div class="nav-links" :class="{ 'show': isMenuOpen || !isMobile }">
        <router-link to="/" class="nav-item" @click="closeMenu">剧本杀复盘</router-link>
        <router-link to="/visualization" class="nav-item" @click="closeMenu">数据可视化</router-link>
        <router-link to="/about" class="nav-item" @click="closeMenu">关于网站</router-link>
      </div>
    </nav>
    
    <router-view v-slot="{ Component }">
      <transition name="page" mode="out-in">
        <component :is="Component" />
      </transition>
    </router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isMenuOpen: false,
      isMobile: false
    }
  },
  created() {
    this.checkScreenSize()
    window.addEventListener('resize', this.checkScreenSize)
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.checkScreenSize)
  },
  methods: {
    checkScreenSize() {
      this.isMobile = window.innerWidth <= 768
    },
    closeMenu() {
      if (this.isMobile) {
        this.isMenuOpen = false
      }
    }
  }
}
</script>

<style>
.app-container {
  min-height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

.nav-menu {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(10px);
  border-radius: 25px;
  padding: 10px 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.menu-icon {
  display: none;
  flex-direction: column;
  gap: 6px;
  cursor: pointer;
  padding: 10px;
}

.menu-icon span {
  display: block;
  width: 25px;
  height: 2px;
  background: var(--text-primary);
  transition: all 0.3s;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-item {
  text-decoration: none;
  color: var(--text-primary);
  padding: 8px 16px;
  border-radius: 20px;
  transition: all var(--transition-speed);
  font-weight: 500;
}

.nav-item:hover {
  background: linear-gradient(135deg, #f9c5a5, #f0b6eb);
  color: white;
  transform: translateY(-2px);
}

.router-link-active {
  background: linear-gradient(135deg, #f9c5a5, #f0b6eb);
  color: white;
}

@media (max-width: 768px) {
  .menu-icon {
    display: flex;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100%;
    right: 0;
    background: rgba(255, 255, 255, 0.95);
    border-radius: 15px;
    padding: 10px;
    margin-top: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  }

  .nav-links.show {
    display: flex;
  }
}
</style> 